"announcement": "All items ready to ship in 1 week, shipping takes 2-3 business days \nLarge order? We have discounts available here: http://etsy.me/1mfUkRw\n$1 Gift Packaging! Add it to your order here: http://etsy.me/1U30zTY\n\nOur leather products are crafted of high quality, durable leather and constructed to our rigorous quality standards to provide you with a sturdy, beautiful, and reliable bag or accessory.\n\nAll orders ship within 1 week via USPS, or sooner if you select a rush upgrade at checkout. Standard shipping to US destinations takes 2-3 business days. Standard international shipping takes up to 15 business days. Tracking information will be provided once your order ships.",
