"policy_shipping": "Please double check the ship-to address before you order. We cannot honor delivery commitments if you provide the wrong address.\r\n\r\nWe do not issue refunds or replacements on orders identified by USPS as delivered.  \r\n\r\nINTERNATIONAL SHIPMENTS:\r\nItems shipped via First Class International USPS mail cannot be tracked and will not be insured if your item is lost in the mail. Under these conditions we cannot offer refund or replacement. Estimated delivery time averages 15 business days from date shipped. When customs clearance procedures are required, it can cause delays beyond our original delivery estimates. International customers are responsible for all import taxes, customs duties and fees levied by the destination country. All international orders will have full value declared on customs forms, and will be marked as merchandise.",
"policy_refunds": "Orders cannot be returned or refunded unless the item is not as described in the listing or we have made a mistake.  In this rare event, your order will be promptly replaced or refunded. Please double check the spelling of any custom embroidery or embossing as we cannot offer a refund or an exchange of a customized product if you have made a mistake while ordering.",
"policy_additional": "Because leather is a natural material, slight variations in color and texture will occur. We do our best to match materials as closely as possible within each order, but we cannot guarantee identical leather between separate orders. All color options will be labelled in each product listing, please check before you select a color as we cannot replace or refund your order if you selected the wrong color.\r\n\r\nAny changes to your order must be made within 24 hours of placing your order. We cannot provide refunds or exchanges if you provided incorrect personalization instructions.",
