"title": "Meriwether of Montana. Inspired Hard Goods for the Home",
"announcement": "Meriwether of Montana was founded in 2012 by Dan Brown and Barb Pfannkuch in the beautiful Rocky Mountain town of Whitefish, Montana. Meriwether designs and sells unique, one of kind gifts for people of all ages (but it helps if you are young at heart.) We have an immense appreciation for well crafted objects and we are drawn toward the nostalgic and the whimsical. Deep down we possess a deep yearning for the Good Ol&#39; Days and desperately wish we could go back...\r\n\r\nMeriwether offers a wonderfully eclectic array of beer gifts, wine gifts and gifts both odd and unique. We require that everything we sell be functional and beautiful (and if it makes you giggle just a bit that works too.) But our primary goal is to make the gift giver a hero -because we know that it feels so amazing when you get to give that perfect gift....\r\n\r\nIf you are in Whitefish, Montana we have a full retail gift boutique right in downtown Whitefish. Our retail store is a peachy (yes... peachy) place to shop for gifts and goodies of all kinds - especially for gifts for dads, gifts for beer lovers, gifts for wine lovers, gifts for coffee lovers... and gifts for people who love to laugh and love to own quality goods. (you get the point...)\r\n\r\nAs a special bonus, many of the gift items we sell can be engraved. Giving an engraved gift is a super way to add that personal touch to your gift. Be sure to read about our personalized gift and engraved gift options.\r\n\r\nAnd what about the name Meriwether? Our name is inspired by Meriwether Lewis... of Lewis and Clark fame. Dan is a huge aficionado of the Lewis and Clark expedition and the Corp of Discovery passed through our neck of the woods on their way to the Pacific coast.",
