"policy_welcome": "Welcome to our humble little Etsy Shop.  Having fun browsing and don&#39;t hesitate to convo us if you have any questions!",
"policy_payment": "We accept either Etsy&#39;s direct checkout or PayPal. However, we can accommodate special payment request if arranged prior to placing your order. \r\n\r\nAttention International customers: we are not responsible for custom duties or import tariffs, taxes, fees or delays caused by customs. Please be aware that you may be required to pay these additional charges when the package reaches your country. You may wish to verify with your local customs department for estimates of these charges and for further information. Please do not ask us to mark as gift or change the declared pricing on the customs form. We are a company that frequently ships international orders and are not able to put false information on customs documentation.\r\n",
"policy_shipping": "Orders generally leave our studio within 2 to 4 business days... add an extra day to this for custom engraved items. If for some reasons this is not possible, we will notify you with the scheduled ship date. All domestic orders are shipped by either FedEx Ground or Home Delivery OR first class USPS. If you&#39;d like insurance, please send a convo and we will get you a quote. \r\n",
"policy_refunds": "Global orders are shipped USPS First-Class Mail International. International shipping usually takes 1-2 weeks in transit, but varies by location (and can take up to a month or more). Tracking information IS NOT usually available and we are not responsible for customs charges, import duties, etc. Please be aware of your country&#39;s regulations before you order. We are not responsible for any lost international mail. If you have any concerns please contact us before you place your order. You may purchase additional shipping insurance for your package if you wish. \r\nInternational orders must be shipped to the confirmed PayPal address.\r\nHoliday shipping: ship-by date recommendations are on our shop announcement.",
"policy_additional": "If you are unhappy with your product, please contact us. We will provide a refund or exchange (domestic only) for unused items in original condition, less shipping charges. Credits or replacement items will not be sent until we receive the item to be returned. We need to be notified of returns or exchanges before you return the item and need to receive items within 30 days of original payment. \r\n\r\nRefunds are made to the original form of payment, and processing times vary.\r\n\r\nDamages, domestic shipping: If your item is lost or broken in shipping, we will happily send a replacement item. Damaged or lost custom printed items will be replaced or refunded at our discretion. \r\n\r\nDamages, international shipping: We do not offer refunds or replacements for damaged items or lost international packages unless you purchase additional shipping insurance at the time of ordering.",
