"sale_message": "Thank you for your business! Please note that all products are carefully handmade one piece at a time. Due to this process, there may be slight variations from one item to the next. These variations are intrinsic to the manufacturing of handmade products and will make your purchase truly one of a kind. ",
"digital_sale_message": null,
