"login_name": "lisasprowls",
"accepts_custom_requests": false,
"policy_welcome": "Welcome to my Etsy shop! I am a native Coloradan, born and raised in the beautiful Denver area. All of my jewelry is handmade with sterling silver, 14k goldfill, copper, semi-precious stones and freshwater pearls. I am a detail-oriented artist that strives to create contrasting textures, dynamic forms, strong color awareness and fine craftsmanship in my work. I frequently challenge myself and learn new techniques to bring to my collections.\r\nPlease contact me if you have any questions! Thanks for looking!",
"policy_payment": "I accept payments via Paypal. I require payment within 48 hours of purchase. If payment is not received, I reserve the right to cancel the transaction. If you for some reason cannot make payment within 48 hours, please contact me. \r\nAll purchases in Florida are subject to state sales tax.",
"policy_shipping": "Packages are shipped by USPS first-class mail with delivery confirmation. Please allow 7 days to receive your package. (Usually 3-4 days). I ship as soon as payment is received and most orders go out within 48 hours of purchase. However, please allow for at least 7 days for shipment in case I need to order special materials. All jewelry is carefully wrapped in tissue and is shipped in a small flat rate box.\r\nI am not responsible for items lost of damaged during shipping.\r\nIf you are in need of faster shipping please contact me and we can make arrangements. This may require an extra shipping charge.",
"policy_refunds": "Returns or exchanges are accepted within 30 days of purchase. All jewelry must be returned in it&#39;s original state with receipt. Buyer is responsible for return shipping costs. \r\nIf you are unsatisfied with a specific part of your purchase, contact me and we can discuss reworking the piece to meet your needs.",
"policy_additional": "Due to the nature of handmade items, some pieces may differ slightly from images on postings. Also please remember that semi-precious stones and freshwater pearls tend to vary in appearance (color and texture). \r\nFeel free to contact me about custom orders! I have completed numerous custom and bridal order requests and am more than happy to work with you!",
