"description": "Personalized Whiskey Groomsman Gifts double as Custom Glasses and Cool Groomsmen Favors. \n\nQuality personalized laser etched Whiskey Glasses from ScissorMill on Etsy.\n\nOur Laser Etched Rocks Glasses are thick walled with a Heavy Base & hold 10oz while featuring original designs by ScissorMill. \n\nQuestions?? - Just send us a Convo! We&#39;re here to help M-F 9am-5pm PST\n\n~This Listing is for (1) Personalized Glasses\n\nHow To Place An Order:\n1. Choose the Quantity needed (use the drop-down menu).\n2. Choose the Design Style (use the drop-down menu).\nNote: Design Styles are shown in the last picture.\n3. Add it to your cart.\n4. Write the Name, Title, Date, etc in the &quot;Note to ScissorMill&quot; when you check out.\n\nListings for Sets:\n(1) Glass https://www.etsy.com/listing/89549904/groomsmen-pilsner-glassesetched-giftbest?ref=shop_home_active_10\nSet of (2) - https://www.etsy.com/listing/225467392/2-groomsmen-glasses-set-of-2-beer-mugs?ref=shop_home_active_23\nSet of (3) - https://www.etsy.com/listing/225470901/set-of-3-groomsmen-gifts-gift-for?ref=shop_home_active_12\nSet of (4) - https://www.etsy.com/listing/225469904/set-of-4-groomsmen-gifts-father-of-the?ref=shop_home_active_15\nSet of (5) - https://www.etsy.com/listing/225473213/set-of-5-groomsmen-gifts-best-man-gift?ref=shop_home_active_20\nSet of (6) - https://www.etsy.com/listing/226511990/set-of-6-groomsmen-gifts-wedding-party?ref=shop_home_active_2\nSet of (7) - https://www.etsy.com/listing/226514295/set-of-7-groomsmen-favors-personalized?ref=shop_home_active_24\nSet of (8) - https://www.etsy.com/listing/226517901/set-of-8-groomsman-gift-ideas-gifts-for?ref=shop_home_active_21\nSet of (9) - https://www.etsy.com/listing/176269073/set-of-9-groomsmen-gift-ideas-best-man?ref=shop_home_active_20\nSet of (10) - https://www.etsy.com/listing/226654798/set-of-10-party-cups-corporate-gift?ref=shop_home_active_24\n\nSpecifications:\n~Rocks Glass is 3.875&quot; tall, holds 10 ounces of liquor, and has a a thick solid base.\n~Glasses are made in the USA by Libbey.\n~Gift Boxes are plain White. (2) Sheets of tissue paper are included for each Gift Box purchased.\n~We personalize each glass for you in our shop located in Temecula, California.\n\nAbout ScissorMill:\nAll of my items are new. I design each one myself. Every Glass is etched by laser. This makes for a truly unique piece you just can&#39;t find anywhere else. \n\nWe ship our personalized glass in USPS Priority Mail & FedEx Home Delivery. Each piece is individually bubble wrapped and all contents are packed tight. Glass is risky to ship, so I always take the time to do the best packing possible. (Rarely accidents happen, so I always save your images just in case another piece is needed.)\n\n By purchasing this listing you are agreeing to the store&#39;s terms found in the &quot;Shipping & Policies&quot; tab within this listing. ",
