"description": "Unique one of a kind metal whiskey 6 oz. flask with option wood box. These personalized etched flask are a certain hit. Perfect groomsmen gift, birthday gift for men, man cave gift. We can engrave anything you want, logos, graphics, saying, etc. We do have graphic ideas, see image #5.  Let us know at checkout in sellers notes how you want engraved. \r\n\r\nFlask Size = 4.5&quot; x 3&quot; x 1&quot;\r\nWood Box Size = 5&quot; x 5.5&quot; x 2.5&quot;\r\n\r\nGraphic Ideas\r\n\r\nImage #5 are some design options available, if you want to use any  of these, please specify the graphic item code at checkout in sellers notes along with customization details. \r\n\r\nQuote Ideas \r\n\r\nThese come personalized with or without whiskey quotes, here&#39;s some ideas:\r\n\r\n1) A drunk mans words are a sober mans thoughts. ~ Steve Fergosi\r\n2) Work is the curse of the drinking classes. ~ Oscar Wilde \r\n3) Reality is an illusion created by a lack of alcohol. ~ N.F. Simpson \r\n4) He that drinks fast, pays slow. ~ Benjamin Franklin \r\n5) Responsible drinking? Now thats an oxymoron. ~ Aaron Howard \r\n6) I know a lot more old drunks than old doctors. ~ Joe E. Lewis \r\n7) Alcoholic friends are as easy to make as Sea Monkeys. ~ Dry \r\n8) Ive never been drunk, but often Ive been overserved. ~ George Gobel\r\n\r\nWithout Box Item Code 024841\r\nWith Box Item Code 024981",
