"title": "Lasers Edge",
"announcement": "Welcome to Lasers Edge! We are pleased to introduce our gift shop for all occasions. Our number one priority is our customers! We strive for excellent customer service! Couldn&#39;t find what you are looking for? Please message us with an idea you have and we&#39;ll work together to make the perfect gift! We LOVE custom orders! Check back often for new designs! ",
"currency_code": "USD",
"is_vacation": false,
"vacation_message": null,
"sale_message": "Thank you so much for your business! The greatest compliment we can receive is the referral of your friends and family. Your feedback is very important to us!",
"digital_sale_message": null,
"last_updated_tsz": 1460660054,
"listing_active_count": 89,
"digital_listing_count": 0,
"login_name": "lasersedge77",
"accepts_custom_requests": true,
"policy_welcome": "Welcome to Lasers Edge! Thank you for visiting our shop. Our #1 goal is to provide excellent customer service. We specialize in creating the perfect personalized gift for that special someone in your life. \r\n\r\nWe LOVE custom orders! Please message us with an idea you have and we&#39;ll work together to make the perfect gift!\r\n\r\nWe Strive for 100% Customer Satisfaction and Will Never Settle for Anything Less!",
"policy_payment": "SECURE OPTIONS\nEtsy keeps your payment information secure. Sellers don't receive your credit card information.\n- Visa\n- MasterCard\n- American Express\n- Discover\n- PayPal\n- Apple Pay\n- SOFORT Bank Transfer\n- iDEAL\n- Etsy Gift Card",
"policy_shipping": "PROCESSING TIME\nThe time I need to prepare an order for shipping varies. For details, see individual items.\n\nESTIMATED SHIPPING TIME\nAustralia: 10 - 15 business days\nNorth America: 3 - 5 business days\nEurope: 10 - 15 business days\nAustralia, New Zealand and Oceania: 10 - 15 business days\nAsia Pacific: 10 - 15 business days\nLatin America and the Caribbean: 10 - 15 business days\nNorth Africa and the Middle East: 10 - 15 business days\nSub-Saharan Africa: 10 - 15 business days\n\nI'll do my best to meet these shipping estimates, but cannot guarantee them. Actual delivery time will depend on the shipping method you choose.\n\nCUSTOMS AND DUTIES FEES\nBuyers are responsible for any customs or duties fees that may apply. Sellers aren't responsible for delays due to customs",
"policy_refunds": "I GLADLY ACCEPT RETURNS AND CANCELLATIONS\nJust contact me within: 14 days of delivery\nShip returns back to me within: 30 days of delivery\nRequest a cancellation within: 24 hours of purchase\n\nI DON'T ACCEPT EXCHANGES\nBut please contact me if you have any problems with your order.\n\nTHE FOLLOWING ITEMS CAN'T BE RETURNED\nBecause of the nature of these items, unless they arrive damaged or defective, I can't accept returns for:\n- Custom or personalized orders\n- Perishable products (like food or flowers)\n- Digital downloads\n- Intimate items (for health/hygiene reasons)\n\nCONDITIONS OF RETURN\nBuyers are responsible for return shipping costs.\nIf the item is not returned in its original condition, the buyer is responsible for any loss in value.",
"policy_additional": "I will only use your shipping and billing address, and contact information\n- To communicate with you about your order\n- To fulfill your order\n- For legal reasons (like paying taxes)\n\nWHAT SHIPPING METHOD DO WE USE?\nAll orders will ship First Class Standard US Mail or USPS Priority Mail. \n\n Shipping times will vary based on your location. Most orders shipped within the United States take 3-5 business days. Orders shipped outside of the United States will take 2-3 weeks depending on your location. ***Please Note: We are not responsible for fees charged by customs for orders shipped outside of the United States. ***\n\n Once Your Order is Complete You Will Receive a Confirmation Email Along with Tracking from USPS.\n\n All orders are shipped from Port Jervis, NY.\n\nRETURNS/EXCHANGES\nDue to the Nature of Our Business, ALL SALES ARE FINAL. Please be Sure to Double Check that Your Engraving Information is Correct Prior to Submitting Your Order.\n\nWHAT IF MY ITEM WAS RECEIVED DAMAGED?\nIn the event you receive a damaged item, please inform us within 48 hours of delivery. Please email a photo of the damaged item along with a description of the damage to be eligible for a free replacement to the following email address: lasersedge77@gmail.com\n\nPLEASE HELP, I RECEIVED AN INCORRECT ITEM OR MY ITEM WAS NOT PERSONALIZED CORRECTLY.\nIn the event you receive an item that was made in error, please send a photograph along with a description of the Item that was made incorrectly and we will mail you a replacement within 48 hours.\n\n ***Please Note*** Please be sure to double check that your personalization information is correct prior to submitting your order. We will personalize your item based on the information that was provided to us during the checkout process.\n\nDO YOU PROVIDE &QUOT;PROOFS&QUOT; OF YOUR DESIGNS?\nWe can absolutely provide a &quot;proof&quot; of the design you choose. However, PROOFS ARE AVAILABLE UPON REQUEST ONLY. To ask for a proof you may simply do so by contacting us via Etsy conversations or via the &quot;Note to LasersEdge77&quot; section of the order.\n\nHOW DO I PROVIDE MY PERSONALIZATION DETAILS?\nYou may provide instructions for personalizing your order in the &quot;Note to LasersEdge77&quot; section of the order. This note section can be viewed once the items have been placed in the shopping cart.",
