"title": "We Can Design For Any Ideas You May Have in Mind! ",
"announcement": "Choose from Our Designs, Submit Your Own, or Have Us Design for You! \n\nInspire our creativity with your design requests or let us inspire you with our portfolio of design and font combinations. We specialize in providing our customers with options from the design to the delivery. Custom orders available for any combination of our products and gift services. We communicate in a timely manner and offer rush orders for any last minute gift purchases. \n\nDESIGN&#39;S THE LIMIT PERSONALIZED GIFTS FOR EVERY OCCASION\nwww.designsthelimit.etsy.com\n\nWe provide gift services as personalized as the gifts we create and can include professional gift wrap with color & ribbon options, premium note cards with customer provided gift messages including envelope, and engraved gift message options for treasured gift purchases. For business customers, we offer engraved logo and contact information on the underside of our cutting boards or on the back of our picture frames for real-estate, travel, or other business professionals.  For every gift giving occasion, personal or business, make it personal and professional at Design&#39;s the Limit.\n\nDESIGN&#39;S FOR ETERNITY WEDDING GIFTS & FAVORS with REGISTRY\nwww.designsforeternity.etsy.com & www.etsy.com/registry\n\n If you are getting married, Design&#39;s the Limit is also on Etsy at Design&#39;s for Eternity! Get the perfect gifts for your wedding party and add us to your Etsy Gift Registry! We have an entire section of our shop devoted to couple&#39;s monograms for the gifts you get for yourself and we fulfill bulk orders for the entire wedding party with individual personalization! \n\n&quot;Don&#39;t take our word for it, read our reviews!&quot; \nwww.etsy.com/your/shops/DesignstheLimit/reviews\n\nDesign&#39;s the Limit offers design and personalization of custom glass, stone, wood, and other materials from personalized gifts to display artwork. We offer a variety of quality product options and accept custom orders for glass, stone, and other materials on a commission basis. We are socially active members of the Etsy community who love to message with our customers and offer free estimates.\n\nGET SOCIAL @DESIGNSTHELIMIT\n Facebook: www.facebook.com/designsthelimit\n Twitter: www.twitter.com/designsthelimit\n Pinterest : www.pinterest.com/designsthelimit\n Google+: plus.google.com/+designsthelimit \n Wanelo: www.wanelo.com/designsthelimit\n Instagram: www.instagram.com/designsthelimit\n Tsu: www.tsu.co/designsthelimit\n Tumblr: www.designsthelimit.tumblr.com \n\nRichard and Tatiana of ETSY SHOP Design&#39;s the Limit\non Etsy at Shop: www.designsthelimit.etsy.com",
"currency_code": "USD",
