"sale_message": "CUSTOMER ACCEPTANCE OF SHOP TERMS AND SHIPPING POLICY:\r\nWhen you purchased your gift from Design&#39;s the Limit you agreed that you read, fully understood, and accepted our shop policies. If you have any questions about our policies, please contact us immediately before we have completed and shipped your gift purchase. Design&#39;s the Limit standard shipping rates include USPS Parcel Select 2-9 days or USPS First Class 2-5 days and neither of these options includes postal insurance. The USPS includes insurance for all USPS Priority Mail and USPS Priority Express Mail shipments, but it is the customers sole responsibility to submit and handle shipping claims directly with the USPS.",
