"title": "Carolina Crazy",
"announcement": "Hi and welcome to Carolina Crazy! Please look around and let us know if there&#39;s anything we can do to help you. Custom orders welcome!",
"currency_code": "USD",
"is_vacation": false,
"vacation_message": null,
"sale_message": "Thank you for your purchase! Please note the production time on the listing and feel free to contact us if you have any questions about your order. \r\n\r\n****If your order requires a proof, please check for it within 48 hours. Once we send out a proof, you have an additional 48 hours to approve. Any proofs not approved within 48 hours will be shipped as the proof was sent and we will not be responsible for errors. It&#39;s crucial that we&#39;re able to get orders out on time so please do not forget to check for proofs!****\r\n\r\nIf there is an issue with your order, please contact us so that we can help resolve the problem! \r\n\r\nThank you, \r\nCarolina Crazy Staff\r\n\r\nFor new products, giveaways and promotions follow us on instagram! @carolinacrazygifts\r\nIf you post a pic of your purchase, be sure to tag us for a discount on your next purchase!",
"digital_sale_message": "If you have any problems downloading your purchase, please feel free to contact us. ",
"last_updated_tsz": 1460659326,
"listing_active_count": 462,
"digital_listing_count": 102,
"login_name": "meganbolick",
"accepts_custom_requests": true,
"policy_welcome": "We strive to provide you with the best high quality vinyl product that will last for years to come. Not only will your accessories be looking fabulous in your custom monograms but so will you. If there is anything that you want and do not see please contact us. We are sure we can accommodate whatever your needs maybe. \r\n",
"policy_payment": "We accept all forms of payment. \r\n\r\n CANCELLATIONS\r\n Due to turnaround time personalization we can not cancel orders\r\n",
"policy_shipping": "We use USPS First Class Mail for all of my domestic packages unless you purchase an upgrade. Tracking is provided on all orders.\r\n\r\n SHIPPING AND HANDLING\r\n\r\n *USPS First Class Mail with tracking (2-5 days)\r\n * USPS Priority Mail (1-3 days) UPGRADED\r\n\r\n ****Upgrading your shipping does not put your order first. If you need a rush order it is an additional $5.00\r\n\r\n ****We have found in our experience that the days for arrival are only estimates. We can not guarantee when your package will arrive nor be held responsible for lost packages. This includes USPS Priority Mail UPGRADE\r\n\r\n ****Since all packages come with tracking please make sure that you have provided me with all the correct shipping information. This also goes for your Paypal account. That is where all orders with Paypal will ship too. If a package shows that it has been delivered I will NOT resend or offer a refund for your merchandise. We do provide insurance for all packages. We suggest that you purchase it.\r\n\r\n ****Please make sure that you have all the correct shipping information. We do charge shipping again if package is returned.\r\n\r\n",
"policy_refunds": "Please double check your order for the correct information that is needed. If there is an error in your initials and it is our fault we will replace it immediately.\r\n\r\nDue to the nature of all our merchandise being personalized we can not accept returns.\r\n\r\nIf there is an issue with your order we must be notified within 3 days of receiving.\r\n\r\nIn the event you mess the decal up while applying, we do NOT offer a refund for this. If you&#39;re unsure how to apply the decal please visit: https://www.youtube.com/watch?v=VyRwCvTcriE for a detailed video tutorial.\r\n",
"policy_additional": "We use the best quality outdoor vinyl that will last for years to come.\r\n\r\nWhen decals are applied to Phones-Laptops- Tumblers and such they will not last as long due to constant handling.\r\n\r\n Vinyl decals must be hand washed.\r\n\r\nAll decals are very easy to apply to almost all surfaces. Should you need help applying, please feel free to contact us.\r\n\r\n Vinyl does not like to stick to rubber, silicon or PCU.\r\n\r\n Decals are removable but not reusable.\r\n\r\n ****The printed pattern decals are all sealed with a clear UV protection to HELP prevent fading and peeling. These decals are made from a different type of vinyl that will not last as long as the solid color vinyl.\r\n\r\nGLASS NOTE: As demand increases or decreases our supplier may or may not be able to meet demand. As this occurs, we reserve the right to change glasses as necessary. The general shape will always be the same (i.e. whiskey glasses will always be whisky glasses and wine will always be wine, we will never substitute types) If you, or the person you are ordering for force an issue with this, please contact us prior to ordering to ensure we have the exact glass you&#39;re looking for. ",
"policy_seller_info": "Megan Bolick, Betsy Harpe, Heather Nelson\r\ncarolinacrazygifts@gmail.com\r\nCharlotte, North Carolina",
